.navigationArea {
  background-color: #09357b;
  color: #ffffff;
  padding: 15px;
}

.navigationArea h1 {
  margin: 0;
}

.navigation {
  overflow: hidden;
}

.navigation div {
  float: left;
  display: block;
  text-align: center;
  padding: 14px 10px;
  text-decoration: none;
  font-size: 17px;
}

.navigation .icon {
  display: none;
  color: white;
}

.navigation .menu {
  padding-bottom: 0;
}
/**/
.navigation .input {
  float: right;
}

@media screen and (max-width: 600px) {
  .navigation div:not(:first-child) {
    display: none;
  }
  .navigation div.icon {
    float: right;
    display: block;
    top: 0;
    margin-top: 5%;
    color: white;
  }
}

@media screen and (max-width: 600px) {
  .navigation.bar {
    position: relative;
  }
  .navigation.bar .icon {
    position: absolute;
    right: 0;
    top: 0;
  }
  .navigation.bar div {
    float: none;
    display: block;
    text-align: left;
  }

  .navigation.bar .menu {
    float: none;
    display: block;
    text-align: left;
    width: 85%;
    padding-bottom: 0;
  }
}

input {
  outline: none;
  background-color: #09357b;
}

input[type="search"] {
  font-family: inherit;
  font-size: 100%;
  border: none;
  border-bottom: solid white;
  padding: 9px 15px 9px 15px;
  width: 0px;

  -webkit-transition: all 0.5s;
  -moz-transition: all 0.5s;
  transition: all 0.5s;
}
input[type="search"]:focus {
  width: 130px;
  background-color: #09357b;
  color: white;

  -webkit-box-shadow: 0 0 5px rgba(109, 207, 246, 0.5);
  -moz-box-shadow: 0 0 5px rgba(109, 207, 246, 0.5);
  box-shadow: 0 0 5px rgba(109, 207, 246, 0.5);
}

input:-moz-placeholder {
  color: #999;
}
input::-webkit-input-placeholder {
  color: #999;
}

h1 {
  color: white;
}
