* {
  box-sizing: border-box;
}
.row {
  padding: 0 7%;
}

.row::after {
  content: "";
  clear: both;
  display: table;
  margin: 0 10% 0 10%;
}

[class*="col-"] {
  float: left;
  padding: 15px;
}

html {
  font-family: "Lucida Sans", sans-serif;
}

